@font-face {
  font-family: "MyFont";
  src: url("./SLDT.otf");
}
@font-face {
  font-family: "MyFont2";
  src: url("./la.otf");
}
@font-face {
  font-family: "MyFont3";
  src: url("./3.ttf");
}
@font-face {
  font-family: "MyFont4";
  src: url("./4.otf");
}
* {
  font-family: "MyFont";
}
.box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 1000;
  .stagger {
    font-size: 80px;
    letter-spacing: 5px;
    color: white;
  }
}

#root {
  height: 110vh;
  width: 100vw;
  background-color: rgb(18, 17, 17);
}
.space {
  margin-right: 20px;
}
h2 {
  color: white;
  font-size: 50px;
  margin: 0;
  padding: 0;
}
body {
 height: 110vh;
  position: relative;
  background-color: #f5f5f5;
  cursor: block !important;
  margin: 0;
    padding: 0;
}
.box2 {
  color: white;
  font-size: 50px;
  display: flex;
  gap: 30px;
  z-index: 100;
}
.ok {
  cursor: pointer;
}
.box3 {
  width: 80%;
  height: 200px;
  display: flex;
  gap: 20px;

  opacity: 1;

  .cont {
    width: 33%;
    height: 100%;
    border-radius: 15px;
    position: relative;

    img {
      width: 100%;
      height: 100%;
      border-radius: 15px;
      object-fit: cover;
      object-position: bottom center;
    }
    video {
      width: 100%;
      height: 100%;
      border-radius: 15px;
      object-fit: cover;
      object-position: bottom center;
    }
    p {
        color: white;
        font-size: 50px;
        position: absolute;
        top: 30%;
        left: 3%;
    }
    span {
        color: white;
        font-size: 30px;
        position: absolute;
        top: 60%;
        left: 3%;
    }
  }
}

svg {
    width: 100px;
    height: 100px;
    border-radius:15%;
}

.competances {
    display: flex;
    flex-direction: column;
    gap: 20px;
    .text{
     
        text-align: center;
        p{
          font-family: "MyFont3" !important;
            color: white;
            font-size: 30px;
        }
    }
}
.contact{
   p {
         color: white;
         font-size: 50px;
         
   }
}
.svg {
    display: flex;
    justify-content: center;
    align-items: center;

}


//media queries 400px
@media screen and (max-width: 800px) {
  body {
    height: 180vh;
  }
  #root {
    height: 180vh;
  }
  .box {
    .stagger {
      font-size: 50px;
    }
  }
  .box2 {
   
    gap: 10px;
    p {
      font-size: 20px;
    }
  }
  .box3 {
    flex-direction: column;
    gap: 10px;
    .cont {
      width: 100%;
      height: 200px;
      p {
        font-size: 50px;
      }
      span {
        font-size: 50px;
      }
    }
  }
  .competances {
    .text {
      p {
        font-size: 20px;
      }
    }
  }
  .contact {
    p {
      font-size: 30px;
    }
  }
  svg {
    width: 50px;
    height: 50px;
  }
  .text {
    p {
      padding:  10px;
    }
  }
  
}
